<template>
  <div id="app">
    <nav class="navbar navbar-light bg-faded">
      <h1 class="navbar-brand mb-0">LOIS STAT API</h1>
    </nav>
    <div class="container">
      <div id="mapid"></div>
      <button  type="button" class="btn btn-secondary btn-lg btn-block" v-on:click="createReport(geometry)">HENT RAPPORT</button>
      <div class="row"></div>
        <div id="report">
          <div v-bind:id="item.SheetName" v-for="item in report">
            <h1>{{item.SheetName}}</h1>
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <td><strong>VARIABEL</strong></td>
                  <td><strong>VÃ†RDI</strong></td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(val, key) in item.Table[0]">
                  <td>{{key}}</td>
                  <td>{{val}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import L from 'leaflet'
import Draw from 'leaflet-draw'

export default {
  name: 'app',
  data () {
    return {
      report: {},
      geometry: {},
      map: {}
    }
  },
  methods: {
    getData: function (geom) {
      let self = this
      // Make a get request
      // let config = {
      //   url: 'http://bal0-lois:8080/api/datapakkeresultat?procedureID=3&procedureParametre=%7B%22UdStraekID%22%3A%221%22%2C%22LS2RapportID%22%3A%223%22%7D',
      //   data: geom
      // }
      axios.post('http://bal0-lois:8080/api/datapakkeresultat?procedureID=3&procedureParametre=%7B%22UdStraekID%22%3A%221%22%2C%22LS2RapportID%22%3A%223%22%7D', geom)
        .then(function (response) {
          self.report = response.data.Result
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    createReport: function (geom) {
      //Check if polygon is drawn
      if (Object.keys(this.geometry).length === 0 && this.geometry.constructor === Object) {
        alert('Du mangler at tegne et omrÃ¥de pÃ¥ kortet')
      } else {
        this.getData(geom)
      }
    }
  },
  mounted: function () {
    //wait for document ready
    this.$nextTick(function () {
      // create a map in the "map" div, set the view to a given place and zoom
      this.map = L.map('mapid').setView([55.731267, 12.363396], 13);
      // add an OpenStreetMap tile layer
      L.tileLayer('https://stamen-tiles-{s}.a.ssl.fastly.net/toner-lite/{z}/{x}/{y}.{ext}', {
        attribution: 'Map tiles by <a href="http://stamen.com">Stamen Design</a>, <a href="http://creativecommons.org/licenses/by/3.0">CC BY 3.0</a> &mdash; Map data &copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>',
        subdomains: 'abcd',
        minZoom: 0,
        maxZoom: 20,
        ext: 'png'
      }).addTo(this.map);

      // Initialize the FeatureGroup to store editable layers
      let drawnItems = new L.FeatureGroup();
      this.map.addLayer(drawnItems);

      // Initialize the draw control and pass it the FeatureGroup of editable layers
      let drawControl = new L.Control.Draw({
          draw: {
              polygon: true,
              marker: false,
              polyline: false,
              rectangle: false,
              circle: false,
              circlemarker: false
          },
          edit: {
              featureGroup: drawnItems
          }
      });
      this.map.addControl(drawControl);

      let self = this
      this.map.on('draw:created', function(e) {
        let type = e.layerType,
            layer = e.layer,
            crs = {
                "type": "name", 
                "properties": {
                    "name": "urn:ogc:def:crs:OGC:1.3:CRS84"
                }
            };

        // get geojson and store in model
        let shape = layer.toGeoJSON()
        self.geometry = shape;
        // add crs def to geojson
        self.geometry.crs = crs;
        
        drawnItems.addLayer(layer);
      });

      this.map.on('draw:edited', function() {
        //STORE CHANGES IN DATA PROP
      });

      this.map.on('draw:deleted', function() {
          // Update db to save latest changes.
          self.geometry = {};
      });


    })
  }
}
</script>

<style>
  @import "../node_modules/leaflet/dist/leaflet.css";
  @import "../node_modules/leaflet-draw/dist/leaflet.draw.css";

  #mapid {
    height: 500px;
  }

  #report {
    padding-top: 30px;
  }
</style>



